@import url('https://fonts.googleapis.com/css2?family=Ubuntu+Condensed&display=swap');
/*

  font-family: 'Ubuntu Condensed', sans-serif;

*/

p {
  margin-right: 6px;
  margin-left: 6px;
}

.App {
  max-width: 700px;  
  margin: auto;
}

h3 {
  margin: auto;
  padding: 1vh;
  font-family: 'Arial Black';
}

.safeArea {
  overflow-x: hidden;
}

.timeWeatherBar {
  display: flex;
  margin: 1vh;
  justify-content: space-between;
  align-items: center;
  font-family: 'Arial Black';
  color: lightgray;
}

.weatherTemp {
  display: flex;
  align-items: center;
}

.horizontalContents {
  display: flex;
  overflow: auto;
  white-space: wrap;
  margin-left: 1vh;
}

.contentSizeForToday {
  min-width: 190px;
  min-height: 190px;
  background-color: rgb(245,245,245);
  margin: 1px;
  display: flex;
  align-items: flex-end;
}

.userContentOpacityLayer {
  font-family: 'Ubuntu Condensed', sans-serif;
  min-width: 190px;
  min-height: 50px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: #fff;
  background: linear-gradient(to bottom, rgba(0,0,0,0), rgba(0,0,0,.5));
}

.contentUserInfoTextLayer {
  display: flex;
  align-items: center;
  margin-left: 6px;
}

.profileImg-1 {
  width: 36px;
  height: 36px;
  border-radius: 50px;
  border: 1px solid white;
}

.naviBar {
  align-items: center;
  margin: 2vh;
  position: -webkit-sticky; /* Safari */
  position: sticky;
  top: 0;
}

.naviBarIcons {
  display: flex;
  border: 3px solid rgb(245,245,245);
  border-radius: 15px;
  background-color: #fff;
  padding: .5rem;
  margin: auto;
  justify-content: space-around;
}

.mood-select {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.tempu-text {
  font-family: 'Arial Black';
  font-size: large;
  padding: .5rem;
}

.flat-content3 {
  margin: auto;
  display: flex;
  justify-content: center;
  width: 100%;
  flex-wrap: wrap;
  border: 1px solid red;
}

.flat-content1 {
  margin: 3px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid blue;
  min-width: 173px;
  min-height: 173px;
}


